<template>
    <Table :columns="columns" :data="data"></Table>
</template>
<script>
    import axios from "axios";
    export default {
         data () {
            return {
                columns: [
                    {
                        title: '订单号',
                        key: 'order_sn'
                    },
                    {
                        title: '业务员',
                        key: 'business_name'
                    },
                    {
                        title: '购买时长',
                        key: 'duration'
                    },
                    {
                        title: '机器编号',
                        key: 'machine_sn'
                    },
                    {
                        title: '支付状态',
                        key: 'order_status'
                    },
                    {
                        title: '订单类型',
                        key: 'order_type'
                    },
                    {
                        title: '支付方式',
                        key: 'pay_type'
                    },
                    {
                        title: '应付金额',
                        key: 'payable_money'
                    },
                    {
                        title: '操作',
                        key: 'action',
                        width: 170,
                        align: 'center',
                        render: (h, params) => {
                            return h('div',[
                                h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.show(params.row);
                                        }
                                    }
                                }, '查看'),
                                h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            let isOk = confirm(`此订单需要扣掉${params.row.payable_money}元余额，是否要支付？`);
                                            if(isOk) {
                                                axios.get("http://"+location.hostname+"/home/customer/payOrderByBalance",{
                                                    order_id: params.row.id
                                                }).then(res => {
                                                    if(res.data.code==1) {
                                                        alert("支付成功");
                                                    } else {
                                                        alert(res.data.msg);
                                                    }
                                                });
                                            }
                                        }
                                    }
                                }, '支付'),
                                h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        // marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            let isOk = confirm("是否要删除此订单？");
                                            if(isOk) {
                                                axios.get("http://"+location.hostname+"/home/customer/delOrder",{
                                                    order_id: params.row.id
                                                }).then(res => {
                                                    if(res.data.code==1) {
                                                        alert("删除成功");
                                                    } else {
                                                        alert(res.data.msg);
                                                    }
                                                });
                                            }
                                        }
                                    }
                                }, '删除')
                            ]);
                        }
                    }
                ],
                data: [
                ]
            }
        },
        methods: {
             show (data) {
                this.$Modal.info({
                    title: '详细信息',
                    content: `
                        订单号: ${data.order_sn}<br />
                        扣款后余额：${data.after_money}<br />
                        扣款前余额：${data.before_money}<br />
                        业务编号：${data.business_sn}<br />
                        业务员: ${data.business_name}<br />
                        购买时长: ${data.duration}<br />
                        机器编号: ${data.machine_sn}<br />
                        备注: ${data.order_note}<br />
                        支付状态: ${data.order_status}<br />
                        订单类型: ${data.order_type}<br />
                        实际金额: ${data.pay_price}<br />
                        支付时间: ${data.pay_time}<br />
                        支付方式: ${data.pay_type}<br />
                        应付金额: ${data.payable_money}<br />
                        资源单价: ${data.price}<br />
                        资源详情: ${data.resource}<br />
                        资源类型: ${data.resource_type}<br />
                        支付流水号: ${data.serial_number}<br />
                        `
                })
            }
        },
        mounted() {
            axios.get("http://"+location.hostname+"/home/customer/orderList").then(res => {
                if(res.data.code==1) {
                    this.data = res.data.data;
                } else {
                    console.error(res.data.msg);
                }
            });
        }
    }
</script>